@import "variable.scss";
@import "typography.scss";
@import "form.scss";
@import "health-listing.scss";
@import "health-details.scss";
@import "ion-slider.scss";
@import "travel-intro.scss";
@import "accident-details.scss";

/*-------------------------
  Faq Page Styles
-------------------------*/

.faq-accordion-area {
    padding: 115px 0;
}

.section-heading {
    text-align: center;
    margin-bottom: 60px;
    .laguage-switcher {
        border: 2px solid #F8F8F8;
        border-radius: 10px;
        display: inline-block;
        height: 55px;
        line-height: 50px;
        .lang-btn {
            padding: 0 58px;
            font-size: 14px;
            font-weight: 500;
            color: $secondary_color;
            border: none;
            background-color: transparent;
            border-radius: 10px;
            &.active {
                background-color: $primary_color;
                color: $paragraph_white;
            }
        }
    }
}

.faq-accordion {
    .accordion-item {
        border: 1px solid rgba(8,40,84,.1);
        border-radius: 10px;
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
        .accordion-header {
            padding: 0 25px 0 20px;
            position: relative;
        }
        .accordion-button {
            background-color: transparent;
            padding: 18px 20px 10px 0;
            border: none;
            box-shadow: none;
            font-size: 20px;
            color: $secondary_color;
            border-bottom: 1px solid rgba(8, 40, 84, 0.05);
            transition: .3s;
            &:after {
                display: none;
            }
            &:before {
                content: "\f107";
                font-family: "Font Awesome 6 Free";
                font-weight: 600;
                background-image: none;
                position: absolute;
                top: 22px;
                right: 0;
                line-height: 1;
                transform: rotate(180deg);
                transition: .3s;
            }
            &.collapsed {
                padding: 20px 20px 20px 0;
                border-bottom: 1px solid transparent;
                &:before {
                    top: 26px;
                    transform: rotate(0deg);
                }
            }
       }
       .accordion-body {
          
           padding: 20px 20px;
           p {
               font-size: 14px;
               margin-bottom: 0;
           }
       }
    }
}

/*-------------------------
  Contact Styles
-------------------------*/
.contact-area {
    padding: 90px 0 120px;
    background-image: url('../images/gradient-bg.svg');   
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.contact-info-box {
    background-color: $paragraph_white;
    border-radius: 10px;
    padding: 90px 45px;
    background-image: url('../images/shape-01.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        li {
            display: flex;
            align-items: center;
            margin-bottom: 60px;
            &:last-child {
                margin-bottom: 0;
            }
            .icon {
                margin-right: 16px;
                text-align: center;
                height: 48px;
                width: 48px;
                line-height: 50px;
                border-radius: 10px;
                font-size: 20px;
                color: $primary_color;
                background: rgba(14, 180, 180, .07);
            }
            .number {
                flex: 1;
                line-height: 1.4;
            }
        }
    }
}

.contact-form-box {
    background-color: $paragraph_white;
    padding: 46px 40px;
    border-radius: 10px;
    .message-department {
        display: inline-flex;
        align-items: center;
        margin-bottom: 35px;
        legend {
            margin-bottom: 0;
            font-size: 18px;
            font-weight: 500;
            color: $secondary_color;
            margin-right: 10px;
        }
        .radio-btn-group {
            display: flex;
            align-items: center;
            .form-check {
                min-height: auto;
                padding-left: 0;
                margin-bottom: 0;
                margin-right: 20px;
                &:last-child {
                    margin-right: 0;
                }
            }
            .form-check-input {
                height: 0;
                width: 0;
                margin: 0;
                border: none;
                &:focus {
                    box-shadow: none;
                }
                &:checked {
                    ~label {
                        &:before {
                            border-color: $primary_color;
                        }
                        &:after {
                            opacity: 1;
                        }
                    }
                }
            }
            .form-check-label {
                position: relative;
                padding-left: 28px;
                cursor: pointer;
                &:before {
                    content: "";
                    height: 18px;
                    width: 18px;
                    border: 1px solid #697883;
                    border-radius: 50%;
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
                &:after {
                    content: "";
                    height: 12px;
                    width: 12px;
                    background-color: $primary_color;
                    border-radius: 50%;
                    position: absolute;
                    top: 7px;
                    left: 3px;
                    opacity: 0;
                }
            }
        }
    }
    .form-field {
        margin-bottom: 16px;
        margin-top: 0;
        &:last-child {
            margin-bottom: 0;
        }
        .form-control {
            height: 48px;
            background-color: #F8FAFC;
            border-radius: 10px;
            font-size: 14px;
            font-weight: 500;
            color: $paragraph_black;
            border: none;
            padding: 12px;
            &:focus {
                border: none;
                box-shadow: none;
            }
            &.textarea {
                height: auto;
            }
        }
        &.form-check {
            padding-left: 0;
            margin-top: 20px;
            .form-check-input {
                height: 14px;
                width: 14px;
                border-color: #082854;
                border-radius: 5px;
                top: 7px;
                position: relative;
                margin: 0;
                &:focus {
                    box-shadow: none;
                }
                &:checked {
                    background-color: $primary_color;
                    border-color: $primary_color;
                }
            }
            .form-check-label {
                font-weight: 500;
               font-size: 16px;
               color: $secondary_color;
               cursor: pointer;
               padding-left: 8px;
            }
        }
    }
    .custom-file-field {
        .file-browse {
            height: 37px;
            width: 75px;
            line-height: 37px;
            background-color: #64748B;
            border-radius: 10px;
            color: #fff;
            font-size: 14px;
            font-weight: 500;
            display: block;
            text-align: center;
            position: absolute;
            right: 6px;
            left: auto;
            pointer-events: none;
        }
    }
}

.contact-info-style-2 {
    padding: 80px 100px;
    background-image: url('../images/shape-03.png');
    background-position: right;
    .contact-list {
        li {
            margin-bottom: 40px;
            ul {
                display: flex;
                align-items: center;
                li {
                    margin-bottom: 0;
                    margin-right: 75px;
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
    }
}

.google-map-area {
    padding: 120px 0;
}

/*-------------------------
 Brand Styles
-------------------------*/
.brand-logo-area {
    padding: 120px 0;
}
.brand-logo-box {
    border: 1px solid rgba(8, 40, 84, 0.1);
    border-radius: 10px;
    padding: 60px;
    text-align: center;
    background-image: url('../images/shape-04.png');
    background-repeat: no-repeat;
    background-position: bottom;
    .heading-title {
        font-size: 18px;
        margin-bottom: 40px;
        span {
            color: $primary_color;
        }
    }
    .brand-box {
        text-align: center;
        padding: 15px;
        img {
            display: inline-block;
            width: 100%;
        }
    }
}



/*-------------------------
  Terms Of Service Styles
-------------------------*/
.terms-service-area {
    padding: 110px 0;
}

.terms-service {
    .section-heading {
        margin-bottom: 0;
    }
    .terms-top-wrap {
        .inner-box {
            margin-bottom: 60px;
            border: 1px solid transparent;
            border-image: linear-gradient(94.61deg, rgba(14, 180, 180, 0.3) 0%, rgba(246, 113, 111, 0.2) 100%) 1;
            box-shadow: 0px 20px 95px rgba(201, 203, 204, 0.3);
            background: #fff;
            padding: 24px 16px;
            border-radius: 10px;
            p {
                font-size: 16px;
                margin-bottom: 30px;
                padding-left: 30px;
                position: relative;
                &:before {
                    content: "\f00c";
                    font-family: "Font Awesome 6 Free";
                    font-weight: 600;
                    font-size: 8px;
                    height: 16px;
                    width: 16px;
                    line-height: 16px;
                    text-align: center;
                    border-radius: 50%;
                    background-color: $primary_color;
                    color: #fff;
                    position: absolute;
                    top: 3px;
                    left: 0;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
    .terms-gradient-heading {
        position: relative;
        padding-top: 60px;
        margin-bottom: 20px;
        background-image: url('../images/shape-02.png');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        .title {
            background: #F5FDFC;
            display: inline-block;
            margin-bottom: 0;
            padding: 12px 25px;
        }

    }
    .terms-order-list {
        list-style: none;
        counter-reset: my-awesome-counter;
        padding: 0;
        margin-bottom: 30px;
        >li {
            counter-increment: my-awesome-counter;
            padding-left: 27px;
            position: relative;
            margin-bottom: 16px;
            &:last-child {
                margin-bottom: 0;
            }
            &:before {
                content: counter(my-awesome-counter) "- ";
                color: $secondary_color;
                font-weight: 500;
                font-size: 20px;
                position: absolute;
                top: -1px;
                left: 0;
            }
        }
        &.order-style-2 {
            >li {
                border-radius: 10px;
                padding: 30px 35px 40px 35px;
                &:before {
                    content: counter(my-awesome-counter) " ";
                    color: #fff;
                    height: 30px;
                    width: 30px;
                    background: linear-gradient(130.66deg, #4FE4E4 0%, #FA8785 100.09%);
                    border-radius: 10px;
                    position: absolute;
                    top: 30px;
                    left: 0;
                    text-align: center;
                }
                .title {
                    margin-bottom: 16px;
                    margin-left: 5px;
                }
                &:nth-child(odd) {
                    background: #FBFFFF;
                    
                }
                &:nth-child(even) {
                    background: #FFFCFC;
                }
                a {
                    color: $primary_color;
                    transition: .3s;
                    &:hover {
                        color: $secondary_color;
                    }
                }
            }

        }
    }
    .terms-unorder-list {
        list-style: disc;
        li {
            margin-bottom: 10px;
            font-size: 16px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    .terms-bg-color-style {
        background: #FFFCFC;
        .terms-gradient-heading {
            margin-bottom: 0;
        }
        p {
            padding: 8px 24px 20px;
            margin-bottom: 0;
        }
        .title {
            background: #F5FDFC;
            padding: 12px 24px;
            display: inline-block;
            margin-bottom: 0;
        }
    }
    
}

/*-------------------------
  Privacy Policy Styles
-------------------------*/
.privacy-policy-area {
    padding: 110px 0;
}
.privacy-policy {
    .terms-gradient-heading {
        margin-bottom: 24px;
        margin-top: 30px;
    }
    a {
        color: $paragraph_black;
        text-decoration: underline;
        transition: .3s;
        &:hover {
            color: $primary_color;
        }
    }
    .unorder-list-box {
        margin: 0 80px 16px;
        padding: 25px 30px;
        box-shadow: 0px 10px 40px rgba(201, 203, 204, 0.1);
        border-radius: 10px;
        border: 1px solid transparent;
        border-image: linear-gradient(94.61deg, rgba(14, 180, 180, 0.3) 0%, rgba(246, 113, 111, 0.2) 100%) 1;
        .terms-unorder-list {
            margin-bottom: 0;
        }
    }
}

/*-------------------------
  Bimafy Claim Styles
-------------------------*/
.bima-claim-area {
    padding: 120px 0;
}
.claim-heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .section-title {
        span {
            color: $primary_color;
        }
    }
    .claim-help {
        margin-bottom: 25px;
        color: $secondary_color;
        a {
            color: $primary_color;
            font-weight: 500;
            text-decoration: underline;
            transition: .3s;
            &:hover {
                color: $secondary_color;
            }
        }
    }
}
.claim-company {
    text-align: center;
    position: relative;
    background: linear-gradient(131.83deg, rgba(178, 243, 243, .4) 0%, rgba(254, 236, 236, .3) 99.21%);
    border: 2px solid;
    border-image-source: linear-gradient(312.31deg, #B0F3F3 0%, #FEEBEB 100%);
    border-radius: 10px;
    .logo {
        padding: 30px 35px;
    }
    .title {
        font-size: 16px;
        margin-bottom: 0;
        background-color: #B2F3F3;
        border-radius: 10px;
        height: 60px;
        line-height: 60px;
    }
    &.enrollment {
        background: linear-gradient(131.83deg, rgba(254, 236, 236, .4) 0%, rgba(178, 243, 243, .4) 99.21%);
        border: 2px solid;
        border-image-source: linear-gradient(312.31deg, #FEEBEB 0%, #B0F3F3 100%);
        .title {
            background-color: #FDD3D3;
        }
    }
}

.claim-form-header {
    padding: 120px 0;
    .header-content {
        .logo {
            margin-bottom: 20px;
        }
        .title {
            background-color: #fff;
            padding: 20px 30px;
            box-shadow: 0px 40px 80px 0px #EBFCFCCC;
            display: inline-block;
            margin-bottom: 0;
        }
    }
}

.claim-form-area {
    padding: 120px 0;
    background: linear-gradient(-138.72deg, rgba(255, 244, 245, 0.7) -34.17%, rgba(232, 250, 250, 0.7) 93.36%);
}

.claim-submit-form {
    .form-info {
        margin-bottom: 60px;
        &:last-child {
            margin-bottom: 0;
        }
        .info-title {
            margin-bottom: 20px;
            color: $primary_color;
            .sl-number {
                color: $accent_color;
                margin-right: 10px;
            }
        }
        .inner {
            background-color: #fff;
            padding: 40px;
            border-radius: 10px;
        }
        .form-field {
            .radio-btn-group {
                margin-top: 7px;
            }
            &.radio-btn-inline {
                display: flex;
                align-items: center;
                >label {
                    margin-bottom: 0;
                    margin-right: 25px;
                    margin-top: 7px;
                    font-size: 18px;
                }
            }
            hr {
                border-color: $dark_grey;
                margin: 6px 0 20px;
            }
        }
    }
    .submit-area {
        margin-top: -30px;
        .form-field {
            margin-top: 18px;
        }
    }
}

/*-------------------------
  User Profile Styles
-------------------------*/
.user-welcome-notice {
    margin-top: 60px;
    padding: 60px 0;
    background-color: rgba(232, 250, 250, 1);
    background: linear-gradient(90deg, rgba(14,180,180,0.06) 0%, rgba(246,113,111,0.06) 100%);
    .welcom-text {
        .title {
            margin-bottom: 0;
        }
    }
}

.user-info-area {
    background: linear-gradient(-138.72deg, rgba(232, 250, 250, 0.7) -34.17%, rgba(255, 244, 245, 0.7) 126.35%);
    padding: 120px 0;
}

.user-profile-info {
    background-color: #fff;
    padding: 40px;
    border-radius: 10px;
    .heading-item {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
        .user-meta {
            display: flex;
            align-items: center;
            background-color: #F8FAFC;
            border-radius: 10px;
            .user-avatar {
                img {
                    border-radius: 10px;
                }
            }
            .user-name {
                padding: 24px;
                margin-bottom: 0;
                flex: 1;
            }
        }
        .action-btn {
            display: flex;
            button {
                margin-right: 12px;
                &:last-child {
                    margin-right: 0;
                }
            }
            .edit-btn {
                background-color: transparent;
                border: 1px solid #EDF2F7;
                border-radius: 6px;
                font-size: 12px;
                color: #16192C;
                height: 45px;
                line-height: 45px;
                padding: 0 20px;
                font-weight: 500;
                transition: .3s;
                i {
                    margin-right: 8px;
                }
                &:hover {
                    background-color: $primary_color;
                    border-color: $primary_color;
                    color: $paragraph_white;
                }
            }
            .submit-btn {
                font-size: 12px;
                letter-spacing: 0.0125em;
                height: 45px;
                line-height: 45px;
                padding: 0 20px;
            }
        }
    }
    .user-full-info {
        .info-title {
            margin-bottom: 24px;
        }
    }
}

/*-------------------------
  Breadcrumb Styles
-------------------------*/
.breadcrumb-area {
    background: radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(18,182,183,0.1) 100%);
    padding: 8px 0;
    .breadcrumb-title {
        margin-bottom: 0;
    }
    .breadcrumb {
        margin: 0;
        justify-content: flex-end;
        li {
            font-size: 16px;
            font-weight: 500;
            color: $primary_color;
            position: relative;
            &:before {
                content: "\f105";
                font-family: "Font Awesome 6 Free";
                font-weight: 600;
                color: $secondary_color;
                padding: 0 14px 0 6px;
            }
            a {
                color: $secondary_color;
                font-weight: 400;
                transition: .3s;
                &:hover {
                    color: $primary_color;
                }
            }
            &:first-child {
                &:before {
                    display: none;
                }
            }
        }
    }
    .breadcrumb-buttons {
        display: flex;
        align-items: center;
        a {
            margin-right: 16px;
            display: inline-block;
            font-size: 16px;
            font-weight: 500;
            color: $secondary_color;
            background-color: #EBFCFC;
            border-radius: 10px;
            height: 44px;
            line-height: 44px;
            padding: 0 25px;
            transition: .3s;
            &:last-child {
                margin-right: 0;
            }
            &:hover {
                background-color: $primary_color;
                color: #fff;
            }
        }
    }
}